export default class PriorityQueue {
    constructor(cmp) {
        this.cmp = cmp;
        this.queue = [];
    }
    get length() {
        return this.queue.length;
    }
    _push(val) {
        const {queue, cmp} = this;
        // 
        let cur = queue.length;
        const ret = queue.push(val);
        // 
        while (cur) {
            // , Math.floor((cur - 1) / 2)
            const parent = (cur - (cur & 1 ? 1 : 2)) / 2;
            // , 
            if (cmp(queue[parent], queue[cur]) <= 0) {
                break;
            }
            // 
            [queue[cur], queue[parent]] = [queue[parent], queue[cur]];
            cur = parent;
        }
        return ret;
    }
    front() {
        return this.queue[0];
    }
    push(...args) {
        args.forEach(val => this._push(val));
    }
    pop() {
        const {queue, cmp} = this;
        // 
        const last = queue.length - 1;
        if (last < 1) {
            return queue.pop();
        }
        [queue[0], queue[last]] = [queue[last], queue[0]];

        let cur = 0;
        let left = 2 * cur + 1;
        let right = left + 1;
        // ()
        while (left < last) {
            // 
            let target = left;
            if (right < last && cmp(queue[right], queue[left]) < 0) {
                target = right;
            }

            // , 
            if (cmp(queue[cur], queue[target]) <= 0) {
                break;
            }
            // 
            [queue[cur], queue[target]] = [queue[target], queue[cur]];
            cur = target;
            left = 2 * cur + 1;
            right = left + 1;
        }
        return queue.pop();
    }
    // 
    // popAll () {
    //     const ret = [];
    //     while(this.length) {
    //         ret.push(this.pop());
    //     }
    //     return ret;
    // }
    // , 
    // *[Symbol.iterator] () {
    //     while(this.length) yield this.pop();
    // }
}
